<template>
  <div style="width: 98vw"><NavBar2/>
    <div class="containerrr">
      
      <div class="head-detail">
        <div class="back-detail">
          <img
            src="https://cdn-icons-png.flaticon.com/512/860/860790.png?w=740&t=st=1685281452~exp=1685282052~hmac=ce43c00291cf7b56d74b446ac5442e73dffce5ebecaf4d187845d164d5d70992"
            width="15px"
            height="15px"
          /><a href="/cassava/naturalenemyvue">กลับ</a>
        </div>
        <div class="threeheader-detail">
          <a style="color: #68a4c4">ศัตรูธรรมชาติ</a>
          <p style="margin-top: 30px;">{{ naturalenemydetail.name }}</p>
          <div class="threeheader-line-detail"></div>
        </div>
      </div>
      <div class="dess">
          <div class="showimage">
            <carousel @next="next" @prev="prev" v-if="naturalenemydetail.image.length>1">
                <carousel-slide v-for="(slide,index) in naturalenemydetail.image" :key="slide" :index="index" :visibleSlide="visibleSlide" :direction="direction">
                    <div style="height: 300px; width:450px"><img :src="'data:image/png;base64,' + slide" style="height: 100%;" /></div>
                </carousel-slide>
            </carousel>
            <div style="height: 300px; width:520px" v-else-if="naturalenemydetail.image.length==1"><img :src="'data:image/png;base64,' + naturalenemydetail.image[0]" style="height: 100%;" /></div>
            <div style="height: 300px; width:520px" v-else><img :src="getResourceImagePath('/img/no-image.jpg')" alt="Example Image" style="height: 100%;"></div>
        </div>
        <div class="detail3">
          <div class="detail-box1">
            <div class="detail-name"><a>รายละเอียดศัตรูธรรมชาติ</a></div>
            <div class="detail-text">
              <div class="detail-left1">
                <ul>
                  <li>ชื่อสามัญ</li>
                  <li>ชื่อวิทยศาสตร์</li>
                  <li>ชนิด</li>
                  <li>การควบคุม</li>
                  <li>อัตราการปล่อย</li>
                </ul>
              </div>
              <div class="detail-right1">
                <ul>
                  <li>{{ naturalenemydetail.naturalEnemy.commonName }}</li>
                  <li>{{ naturalenemydetail.naturalEnemy.scientificName }}</li>
                  <li>{{ naturalenemydetail.naturalEnemy.type }}</li>
                  <li>{{ naturalenemydetail.naturalEnemy.controlMethod }}</li>
                  <li>
                    {{ naturalenemydetail.naturalEnemy.releaseRate }}
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Carousel from './Carousel.vue';
import CarouselSlide from './CarouselSlide.vue';
import NaturalEnemyService from "../service/NaturalEnemyService";
import NavBar2 from "./NavBar2.vue";
export default {
  name: "NaturalEnemyDetail",
  data() {
    return {
      visibleSlide : 0,
            direction: 'left',
      naturalenemydetail: [],
    };
  },
  computed:{
        slidesLen(){
            return this.naturalenemydetail.image.length;
        },
        resources() {
      // Modify the resources path as per your project structure
      return '/cassava/resources';},
    },
  methods: {
    next(){
            if(this.visibleSlide >= this.slidesLen - 1){
                this.visibleSlide = 0;
            }else{
                this.visibleSlide++;
            }
            this.direction = "left"
        },
        prev(){
            if(this.visibleSlide <= 0){
                this.visibleSlide = this.slidesLen-1;
            }else{
                this.visibleSlide--;
            }
            this.direction = "right"
        },
        getNaturalEnemyById(data) {
          NaturalEnemyService.getNaturalEnemyById(data).then((response) => {
        setTimeout(() => {
          console.log(response.data.body.naturalEnemy.commonName)
          this.naturalenemydetail = response.data.body;
          this.isVisible = true
        }, 20)
      });
    },
    getResourceImagePath(filename) {
      return this.resources + filename;
    }
  },
  created() {
    this.getNaturalEnemyById(this.$route.params.naturalenemyId);
  },
  components:{
        Carousel,
        CarouselSlide,
        NavBar2,
    }
};
</script>
<style>
@import url("../../public/resources/frontend/css/detail.css");
