import axios from "axios";

const protocol = window.location.protocol;
const serverPath = protocol + "//www.munbot.org/";

const DISEASE_API_BASE_URL = serverPath+"cassava/v2/api/disease";
const DISEASEID_API_BASE_URL = serverPath+"cassava/v2/api/diseaseid";

class DiseaseService {
  getAllDiseases() {
    return axios.get(DISEASE_API_BASE_URL);
  }

  getDiseaseById(data) {
    return axios.get(DISEASEID_API_BASE_URL + "/" + data);
  }
}

export default new DiseaseService();
