import axios from "axios";
const protocol = window.location.protocol;
const serverPath = protocol + "//www.munbot.org/";

const NATURALENEMY_API_BASE_URL =
serverPath+"cassava/v2/api/naturalenemy";
const NATURALENEMYID_API_BASE_URL =
serverPath+"cassava/v2/api/naturalenemyid";

class NaturalEnemyService {
  getAllNaturalEnemies() {
    return axios.get(NATURALENEMY_API_BASE_URL);
  }

  getNaturalEnemyById(data) {
    return axios.get(NATURALENEMYID_API_BASE_URL + "/" + data);
  }
}

export default new NaturalEnemyService();
