import axios from "axios";

const protocol = window.location.protocol;
const serverPath = protocol + "//www.munbot.org/";
const USER_API_BASE_URL = serverPath+"cassava/v2/api/countuser";

class CountUserService {
  getAllCountUser() {
    return axios.get(USER_API_BASE_URL);
  }
}

export default new CountUserService();
