<template>
  <div>
      <section class="pic-gallery" id="gall" >
        <div class="row" data-aos="fade-up" style="width: 100%;">
          <div class="pic-container">
            <div class="threeheader">
              <p>ภาพโรคมันสำปะหลัง ศัตรูพืช และศัตรูธรรมชาติ</p>
            </div>
            <div class="pic-row" style="display: flex; justify-content:center">
              <div class="pic-gallery-filter" style="width:fit-content; margin:0px; padding:0px;">
                <span
                  class="pic-filter-item"
                  data-filter="all" style="padding: 1vw; margin:0px; border-right: 1px solid grey;"
                  @click="updatePictureAll()" :style="{ backgroundColor: currentBackgroundColor1 }"
                >
                  <a style="font-size: 1.2vw;">ทั้งหมด</a>
                </span>
                <span
                  class="pic-filter-item"
                  data-filter="shoe"  style="padding: 1vw; margin:0px; border-right: 1px solid grey;"
                  @click="updatePictureVariety()"  :style="{ backgroundColor: currentBackgroundColor2 }"
                >
                  <a  style="font-size: 1.2vw;">พันธุ์มันสำปะหลัง</a>
                </span>
                <span
                  class="pic-filter-item"
                  data-filter="natural"  style="padding: 1vw; margin:0px; border-right: 1px solid grey;"
                  @click="updatePictureDisease()" :style="{ backgroundColor: currentBackgroundColor3 }"
                >
                  <a  style="font-size: 1.2vw;">โรคมันสำปะหลัง</a>
                </span>
                <span
                  class="pic-filter-item"
                  data-filter="natural"  style="padding: 1vw; margin:0px; border-right: 1px solid grey;"
                  @click="updatePicturePest()"  :style="{ backgroundColor: currentBackgroundColor4 }"
                >
                  <a  style="font-size: 1.2vw;">ศัตรูพืช</a>
                </span>
                <span
                  class="pic-filter-item"
                  data-filter="natural"  style="padding: 1vw; margin:0px;"
                  @click="updatePictureNaturalEnenemy()" :style="{ backgroundColor: currentBackgroundColor5 }"
                >
                  <a  style="font-size: 1.2vw;">ศัตรูธรรมชาติ</a>
                </span>
              </div>
            </div>
            <!-- gallery item start -->
            <div class="pic-row">
              <!-- all -->
              <div class="pic-gallery-item shoe" v-if="pic == 'all'" v-for="a of all" v-bind:key="a.image" >
                <div class="pic-gallery-item-inner">
                  <div class="fill"><img :src="'data:image/png;base64,' + a.image" /></div><a>{{ a.name }}</a>
                </div>
              </div>
              <!-- variety -->
              <div class="pic-gallery-item shoe" v-if="pic == 'variety'" v-for="a of variety" v-bind:key="a.image" >
                <div class="pic-gallery-item-inner">
                  <div class="fill"><img :src="'data:image/png;base64,' + a.image" /></div>
                  <a>{{a.name}}</a>
                </div>
              </div>
              <!-- disease -->
              <div class="pic-gallery-item shoe" v-if="pic == 'disease'" v-for="a of disease" v-bind:key="a.image" >
                <div class="pic-gallery-item-inner">
                  <div class="fill"><img :src="'data:image/png;base64,' + a.image" /></div>
                  <a>{{a.name}}</a>
                </div>
              </div>
              <!-- pest -->
              <div class="pic-gallery-item shoe" v-if="pic == 'pest'" v-for="a of pest" v-bind:key="a.image" >
                <div class="pic-gallery-item-inner">
                  <div class="fill"><img :src="'data:image/png;base64,' + a.image" /></div>
                  <a>{{a.name}}</a>
                </div>
              </div>
              <!-- naturalenemy -->
              <div class="pic-gallery-item shoe" v-if="pic == 'naturalenemy'" v-for="a of naturalenemy" v-bind:key="a.image" >
                <div class="pic-gallery-item-inner">
                  <div class="fill"><img :src="'data:image/png;base64,' + a.image" /></div>
                  <a>{{a.name}}</a>
                </div>
              </div>
              </div>
              <!-- gallery item end -->
          </div>
        </div>
      </section>
  </div>
</template>

<script>
import PictureService from "../service/PictureService";
export default {
  name: "Picture",
  data() {
    return {
      pic: "all",
      all:[],
      variety: [],
      naturalenemy: [],
      disease: [],
      pest: [],
      currentBackgroundColor1: '#0b212d',
      currentBackgroundColor2: 'aliceblue',
      currentBackgroundColor3: 'aliceblue',
      currentBackgroundColor4: 'aliceblue',
      currentBackgroundColor5: 'aliceblue',
    };
  },
  methods: {
    updatePictureAll() {
      this.pic = "all";
      this.currentBackgroundColor1 = '#0b212d';
      this.currentBackgroundColor2 = 'aliceblue';
      this.currentBackgroundColor3 = 'aliceblue';
      this.currentBackgroundColor4 = 'aliceblue';
      this.currentBackgroundColor5 = 'aliceblue';
    },
    updatePictureVariety() {
      this.pic = "variety";
      this.currentBackgroundColor2 = '#0b212d';
      this.currentBackgroundColor1 = 'aliceblue';
      this.currentBackgroundColor3 = 'aliceblue';
      this.currentBackgroundColor4 = 'aliceblue';
      this.currentBackgroundColor5 = 'aliceblue';
    },
    updatePictureDisease() {
      this.pic = "disease";
      this.currentBackgroundColor3 = '#0b212d';
      this.currentBackgroundColor2 = 'aliceblue';
      this.currentBackgroundColor1 = 'aliceblue';
      this.currentBackgroundColor4 = 'aliceblue';
      this.currentBackgroundColor5 = 'aliceblue';
    },
    updatePicturePest() {
      this.pic = "pest";
      this.currentBackgroundColor4 = '#0b212d';
      this.currentBackgroundColor2 = 'aliceblue';
      this.currentBackgroundColor3 = 'aliceblue';
      this.currentBackgroundColor1 = 'aliceblue';
      this.currentBackgroundColor5 = 'aliceblue';
    },
    updatePictureNaturalEnenemy() {
      this.pic = "naturalenemy";
      this.isUnderline5 = !this.isUnderline5;
      this.currentBackgroundColor5 = '#0b212d';
      this.currentBackgroundColor2 = 'aliceblue';
      this.currentBackgroundColor3 = 'aliceblue';
      this.currentBackgroundColor4 = 'aliceblue';
      this.currentBackgroundColor1 = 'aliceblue';
    },
    getPictureAll() {
      PictureService.getPictureAll().then((response) => {
        this.all = response.data.body;
      });
    },
    getPictureVariety() {
      PictureService.getPictureVariety().then((response) => {
        this.variety = response.data.body;
      });
    },
    getPictureDisease() {
      PictureService.getPictureDisease().then((response) => {
        this.disease = response.data.body;
      });
    },
    getPicturePest() {
      PictureService.getPicturePest().then((response) => {
        this.pest = response.data.body;
      });
    },
    getPictureNaturalenemy() {
      PictureService.getPictureNaturalenemy().then((response) => {
        this.naturalenemy = response.data.body;
      });
    },
  },
  created() {
    this.getPictureAll();
    this.getPictureVariety();
    this.getPictureDisease();
    this.getPicturePest();
    this.getPictureNaturalenemy();
  },
};
</script>

<style>
@import url(../../public/resources/frontend/css/style.css);
</style>
