<template>
  <div style="width: 98vw"><NavBar2/>
    <div class="containerrr">
      
    <div style="height: 70px; width:100%"></div>
      <div class="head-detail">
        <div class="back-detail">
          <img
            src="https://cdn-icons-png.flaticon.com/512/860/860790.png?w=740&t=st=1685281452~exp=1685282052~hmac=ce43c00291cf7b56d74b446ac5442e73dffce5ebecaf4d187845d164d5d70992"
            width="15px"
            height="15px"
          /><a href="/cassava/pestvue">กลับ</a>
        </div>
        <div class="threeheader-detail">
          <a style="color: #68a4c4">ศัตรูพืช</a>
          <p style="margin-top: 20px;">{{ pest.name }}</p>
          <div class="threeheader-line-detail"></div>
        </div>
      </div>
      <div class="des5" style="margin-top:10vh">
        <div class="pic1">
          <div class="showimage">
            <carousel @next="next" @prev="prev" v-if="pest.image.length>1">
                <carousel-slide v-for="(slide,index) in pest.image" :key="slide" :index="index" :visibleSlide="visibleSlide" :direction="direction">
                    <div style="height: 300px; width:400px"><img :src="'data:image/png;base64,' + slide" style="height: 100%;" /></div>
                </carousel-slide>
            </carousel>
            <div style="height: 300px; width:520px" v-else-if="pest.image.length==1"><img :src="'data:image/png;base64,' + pest.image[0]" style="height: 100%;" /></div>
            <div style="height: 300px; width:520px" v-else><img :src="getResourceImagePath('/img/no-image.jpg')" alt="Example Image" style="height: 100%;"></div>
        </div>
        </div>
        <div class="detail2">
          <div class="detail-box2">
            <div class="detail-name"><a>สารป้องกันกำจัดศัตรูพืช</a></div>
            <div class="detail-list">
              <a>ชื่อสามัญ</a><a>%สารออกฤทธิ์และสูตร</a
              ><a>กลุ่มกลไกการออกฤทธิ์</a><a>ความเป็นพิษ</a><a>อัตราการใช้</a>
            </div>
            <div class="detail-text1">
              <div
                class="detail-lefttwo"
                v-for="items of pest.pestManagement"
                v-bind:key="items.pestManagementId"
              >
                <a>{{ items.pesticideName }}</a
                ><a>{{ items.percentApi }}</a
                ><a>{{ items.pesticideMechanism }}</a
                ><a>{{ items.toxicityLevel }}</a
                ><a>{{ items.mixingRatio }}</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Carousel from './Carousel.vue';
import CarouselSlide from './CarouselSlide.vue';
import PestService from "../service/PestService";
import NavBar2 from "./NavBar2.vue";
export default {
  name: "PestDetail",
  data() {
    return {
      visibleSlide : 0,
            direction: 'left',
      pest: [],
    };
  },
  computed:{
        slidesLen(){
            return this.pest.image.length;
        },
        resources() {
      // Modify the resources path as per your project structure
      return '/cassava/resources';},
    },
  methods: {
    next(){
            if(this.visibleSlide >= this.slidesLen - 1){
                this.visibleSlide = 0;
            }else{
                this.visibleSlide++;
            }
            this.direction = "left"
        },
        prev(){
            if(this.visibleSlide <= 0){
                this.visibleSlide = this.slidesLen-1;
            }else{
                this.visibleSlide--;
            }
            this.direction = "right"
        },
        getPestById(data) {
            PestService.getPestById(data).then((response) => {
        setTimeout(() => {
          this.pest = response.data.body;
          this.isVisible = true
        }, 10)
      });
    },
    getResourceImagePath(filename) {
      return this.resources + filename;
    }
  },
  created() {
    this.getPestById(this.$route.params.pestId);
  },
  components:{
        Carousel,
        CarouselSlide,
        NavBar2,
    }
};
</script>
