</template>
<script>
import DiseaseService from "../service/DiseaseService";
import Carousel from './Carousel.vue';
import CarouselSlide from './CarouselSlide.vue';
import NavBar2 from "./NavBar2.vue";
export default {
  name: "DiseaseDetail",
  data() {
    return {
      disease: [],
      visibleSlide: 0,
      direction: 'left',
    };
  },
  computed: {
    slidesLen() {
      return this.disease.image.length;
    },
    resources() {
      // Modify the resources path as per your project structure
      return '/cassava/resources';
    }
  },
  methods: {
    next() {
      if (this.visibleSlide >= this.slidesLen - 1) {
        this.visibleSlide = 0;
      } else {
        this.visibleSlide++;
      }
      this.direction = "left"
    },
    prev() {
      if (this.visibleSlide <= 0) {
        this.visibleSlide = this.slidesLen - 1;
      } else {
        this.visibleSlide--;
      }
      this.direction = "right"
    },
    getDiseaseById(data) {
      DiseaseService.getDiseaseById(data).then((response) => {
        setTimeout(() => {
          this.disease = response.data.body;
          this.isVisible = true
        }, 10)
      });
    },
    getResourceImagePath(filename) {
      return this.resources + filename;
    }
  },
  created() {
    this.getDiseaseById(this.$route.params.diseaseId);
  },
  components: {
    Carousel,
    CarouselSlide,
    NavBar2,
  }
};
</script>
