import axios from "axios";

const protocol = window.location.protocol;
const serverPath = protocol + "//www.munbot.org/";

const VARIETY_API_BASE_URL = serverPath+"cassava/v2/api/variety";
const VARIETYID_API_BASE_URL = serverPath+"cassava/v2/api/varietyid";

class VarietyService {
  getAllVarieties() {
    return axios.get(VARIETY_API_BASE_URL);
  }

  getVarietyById(data) {
    return axios.get(VARIETYID_API_BASE_URL + "/" + data);
  }
}

export default new VarietyService();
