import axios from "axios";

const protocol = window.location.protocol;
const serverPath = protocol + "//www.munbot.org/";

const PEST_API_BASE_URL = serverPath+"cassava/v2/api/pest";
const PESTID_API_BASE_URL = serverPath+"cassava/v2/api/pestid";

class PestService {
  getAllPests() {
    return axios.get(PEST_API_BASE_URL);
  }

  getPestById(data) {
    return axios.get(PESTID_API_BASE_URL + "/" + data);
  }
}

export default new PestService();
