    </template>
  <script>
  export default {
    name: "Developer",
    data() {
    return {
      layout: "webDev",
      isUnderline1: true,
      isUnderline2: false,
      base64Images: {}, // Base64 data of the images
      
    };
  },
  computed: {
    resources() {
      // Modify the resources path as per your project structure
      return '/cassava/resources';
    }
  },
  methods: {
    updateLayoutMobileDev() {
      this.layout = "mobileDev";
      this.isUnderline2 = true;
      this.isUnderline1 = false;
    },
    updateLayoutWebDev() {
      this.layout = "webDev";
      this.isUnderline2 = false;
      this.isUnderline1 = true;
    },
    async convertImagesToBase64(imagePaths) {
      for (const imagePath of imagePaths) {
        const imageSrc = this.getImagePath(imagePath);
        const response = await fetch(imageSrc);
        const blob = await response.blob();
        const reader = new FileReader();

        reader.onloadend = () => {
          this.base64Images = { ...this.base64Images, [imagePath]: reader.result };
        };

        reader.readAsDataURL(blob);
      }
    },
    getResourceImagePath(filename) {
      return this.resources + filename;
    }
  },
  };
  </script>
