import axios from "axios";

const protocol = window.location.protocol;
const serverPath = protocol + "//www.munbot.org/";

const PICTURE_ALL_API_BASE_URL = serverPath+"cassava/v2/api/images";
const PICTURE_VARIETY_API_BASE_URL = serverPath+"cassava/v2/api/variety/image/page/1/value/8";
const PICTURE_DISEASE_API_BASE_URL = serverPath+"cassava/v2/api/disease/image/page/1/value/8";
const PICTURE_PEST_API_BASE_URL = serverPath+"cassava/v2/api/pest/image/page/1/value/8";
const PICTURE_NATURALENEMY_API_BASE_URL = serverPath+"cassava/v2/api/naturalenemy/image/page/1/value/8";

class PictureService {
  getPictureAll() {
    return axios.get(PICTURE_ALL_API_BASE_URL);
  }

  getPictureVariety() {
    return axios.get(PICTURE_VARIETY_API_BASE_URL);
  }

  getPictureDisease() {
    return axios.get(PICTURE_DISEASE_API_BASE_URL);
  }

  getPicturePest() {
    return axios.get(PICTURE_PEST_API_BASE_URL);
  }

  getPictureNaturalenemy() {
    return axios.get(PICTURE_NATURALENEMY_API_BASE_URL);
  }

}

export default new PictureService();
