import { createRouter, createWebHistory } from "vue-router";
import AllView from "@/views/AllView.vue";
import Variety from "@/views/Variety.vue";
import VarietyDetail from "@/views/VarietyDetail.vue";
import Disease from "@/views/Disease.vue";
import Pest from "@/views/Pest.vue";
import NaturalEnemy from "@/views/NaturalEnemy.vue";
import DiseaseDetail from "@/views/DiseaseDetail.vue";
import PestDetail from "@/views/PestDetail.vue";
import NaturalEnemyDetail from "@/views/NaturalEnemyDetail.vue";
import Survey from "@/views/Survey.vue";
import Developer from "@/views/Developer.vue";

const routeList = [
  {
    path: "/cassava/",
    component: AllView,
  },
  {
    path: "/cassava/varietyvue",
    component: Variety,
  },
  {
    path: "/cassava/varietyvue/:varietyId",
    component: VarietyDetail,
  },
  {
    path: "/cassava/diseasevue",
    component: Disease,
  },
  {
    path: "/cassava/diseasevue/:diseaseId",
    component: DiseaseDetail,
  },
  {
    path: "/cassava/pestvue",
    component: Pest,
  },
  {
    path: "/cassava/pestvue/:pestId",
    component: PestDetail,
  },
  {
    path: "/cassava/naturalenemyvue",
    component: NaturalEnemy,
  },
  {
    path: "/cassava/naturalenemyvue/:naturalenemyId",
    component: NaturalEnemyDetail,
  },
  {
    path: "/cassava/mapvue/",
    component: Survey,
  },
  {
    path: "/cassava/devvue",
    component: Developer,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes: routeList,
});



//const app = Vue.createApp({})
