<template>
  <div style="width: 98vw" v-if="isVisible">
    <div class="containerr">
      <NavBar2/>
    <div style="height: 10vh; width:100%"></div>
      <div class="head-detail">
        <div class="back-detail">
          <img
          src="https://cdn-icons-png.flaticon.com/512/860/860790.png?w=740&t=st=1685281452~exp=1685282052~hmac=ce43c00291cf7b56d74b446ac5442e73dffce5ebecaf4d187845d164d5d70992"
          width="15px"
          height="15px"
        /><a href="/cassava/varietyvue">กลับ</a>
        </div>
        <div class="threeheader-detail">
          <a style="color: #68a4c4">พันธุ์มันสำปะหลัง</a>
          <p style="margin-top: 20px;">{{ varietydetail.name }}</p>
          <div class="threeheader-line-detail"></div>
        </div>
      </div>
      <div class="des5">
        <div class="pic1">
          <div class="showimage">
            <carousel @next="next" @prev="prev" v-if="varietydetail.image.length>1">
                <carousel-slide v-for="(slide,index) in varietydetail.image" :key="slide" :index="index" :visibleSlide="visibleSlide" :direction="direction">
                    <div style="height: 300px; width:450px"><img :src="'data:image/png;base64,' + slide" style="height: 100%;" /></div>
                </carousel-slide>
            </carousel>
            <div style="height: 300px; width:520px" v-else-if="varietydetail.image.length==1"><img :src="'data:image/png;base64,' + varietydetail.image[0]" style="height: 100%;" /></div>
            <div style="height: 300px; width:520px" v-else><img :src="getResourceImagePath('/img/no-image.jpg')" alt="Example Image" style="height: 100%;"></div>
        </div>
        </div>
        <div class="detail4">
          <div class="detail-box">
            <div class="detail-name"><a>ยอดและใบ</a></div>
            <div class="detail-text">
              <div class="detail-left">
                <ul>
                  <li>ขนที่ยอดอ่อน</li>
                  <li>สียอดอ่อน</li>
                  <li>สีใบอ่อน</li>
                  <li>สีก้านใบ</li>
                  <li>ลักษณะแฉกกลางใบ</li>
                </ul>
              </div>
              <div class="detail-right">
                <ul>
                  <li>{{ varietydetail.variety.trichome }}</li>
                  <li>{{ varietydetail.variety.apicalLeavesColor }}</li>
                  <li>{{ varietydetail.variety.youngLeavesColor }}</li>
                  <li>{{ varietydetail.variety.petioleColor }}</li>
                  <li>{{ varietydetail.variety.centralLeafletShape }}</li>
                </ul>
              </div>
            </div>
          </div>
          <div class="detail-box">
            <div class="detail-name"><a>ลำต้นและการแตกกิ่ง</a></div>
            <div class="detail-text">
              <div class="detail-left">
                <ul>
                  <li>การแตกกิ่ง</li>
                  <li>ความสูงของการแตกกิ่งแรก(ซม.)</li>
                  <li>ความสูงของลำต้น(ซม.)</li>
                  <li>สีลำต้น</li>
                </ul>
              </div>
              <div class="detail-right">
                <ul>
                  <li>{{ varietydetail.variety.branchingHabit }}</li>
                  <li>{{ varietydetail.variety.heightToFirstBranching }}</li>
                  <li>{{ varietydetail.variety.plantHeight }}</li>
                  <li>{{ varietydetail.variety.stemColor }}</li>
                </ul>
              </div>
            </div>
          </div>
          <div class="detail-box">
            <div class="detail-name"><a>แป้งและผลผลิต</a></div>
            <div class="detail-text">
              <div class="detail-left">
                <ul>
                  <li>เปอร์เซนต์แป้ง(ฤดูฝน)</li>
                  <li>เปอร์เซนต์แป้ง(ฤดูแล้ง)</li>
                  <li>ผลผลิต(กก.)</li>
                  <li>ข้อจำกัด</li>
                </ul>
              </div>
              <div class="detail-right">
                <ul>
                  <li>{{ varietydetail.variety.starchContentRainySeason }}</li>
                  <li>{{ varietydetail.variety.starchContentDrySeason }}</li>
                  <li>{{ varietydetail.variety.rootYield }}</li>
                  <li>{{ varietydetail.variety.limitationNote }}</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import VarietyService from "../service/VarietyService";
import Carousel from './Carousel.vue';
import CarouselSlide from './CarouselSlide.vue';
import NavBar2 from "./NavBar2.vue";
export default {
  name: "VarietyDetail",
  data() {
    return {
      isVisible: false,
      varietydetail: [],
      visibleSlide : 0,
      direction: 'left',
    };
  },
  computed:{
        slidesLen(){
            return this.varietydetail.image.length;
        },
        resources() {
      // Modify the resources path as per your project structure
      return '/cassava/resources';},
    },
  methods: {
    next(){
            if(this.visibleSlide >= this.slidesLen - 1){
                this.visibleSlide = 0;
            }else{
                this.visibleSlide++;
            }
            this.direction = "left"
        },
        prev(){
            if(this.visibleSlide <= 0){
                this.visibleSlide = this.slidesLen-1;
            }else{
                this.visibleSlide--;
            }
            this.direction = "right"
        },
    getVarietyById(data) {
      VarietyService.getVarietyById(data).then((response) => {
        setTimeout(() => {
          this.varietydetail = response.data.body;
          this.isVisible = true
        }, 10)
      });
    },
    getResourceImagePath(filename) {
      return this.resources + filename;
    }
  },
  created() {
    this.getVarietyById(this.$route.params.varietyId);
  },
  components:{
        Carousel,
        CarouselSlide,NavBar2,
    }
};
</script>
<style>
@import url("../../public/resources/frontend/css/detail.css");
